<template>
    <!--订单内容-->
    <div class="_shopOrder d-flex justify-content-center align-items-start ">
        <div id="_Order-box" class="_Order-box card card-body px-0 py-1 py-md-2 mx-0 ">
            <!--订单选项栏-->
            <ul class="_tab-order txt-body list-unstyled d-flex justify-content-around align-items-center pt-1 pt-md-2">
                <li @click="onChangeTab('全部',0)" class="active">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">全部</span>
                </li>
                <li @click="onChangeTab('待付款',1)">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">待付款</span>
                </li>
                <li @click="onChangeTab('已支付',2)">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">待发货</span>
                </li>
                <li @click="onChangeTab('发货',3)">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">待收货</span>
                </li>
                <li @click="onChangeTab('交易完成',4)">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">待评价</span>
                </li>
                <li @click="onChangeTab('过期',5)">
                    <span class=" py-2 py-sm-3 px-1 px-sm-2 text-truncate">已过期</span>
                </li>
            </ul>
            <!--订单商品栏-->
            <div class="content py-1 py-md-3" >
                <goods-item :allList = "allList" :path="$route.fullPath">
                    <template v-slot:numSlot="subdata">
                        <span class="_myNum txt-body px-5  ">x{{subdata.item.num}}</span>
                    </template>
                    <template v-slot:bottom="subdata">
                        <div class="d-flex justify-content-end align-items-end px-3 py-1 text-muted txt-subbody">
                            <div class="px-1">
                                总价
                                <small>￥</small>
                                <span class="txt-subbody">{{(subdata.item.goods.price+2) * subdata.item.num}}</span>
                                <small>.00,</small>
                            </div>
                            <div class="px-1">
                                优惠
                                <small>￥</small>
                                <span class="txt-subbody" v-if="subdata.item.num===1">2</span>
                                <span class="txt-subbody" v-else>{{subdata.item.num}}x2</span>
                                <small>.00,</small>
                            </div>
                            <div class="px-1 text-black " style="font-weight:bold">
                                实付款
                                <small>￥</small>
                                <span class="txt-subbody">{{(subdata.item.goods.price)  * subdata.item.num}} </span>
                                <small>.00</small>
                            </div>
                        </div>
                        <div class="d-flex justify-content-around align-items-center pb-1">
                            <span class="txt-mini text-muted"  @click="$router.push({path: '/orderdetail', query: {'id':subdata.item.id}})" >更多</span>
                            <span class="txt-subbody btn rounded rounded-pill px-2 py-1"  @click="$router.push({path: '/orderdetail', query: {'id':subdata.item.id}})"
                                  style="color:#5D6A68; border:2px solid #BAF3EA">查看详情</span>
                        </div>
                    </template>
                </goods-item>
                <div v-if="!allList.length" class="d-flex flex-column justify-content-center align-items-center" style="height:400px;">
                    <img src="~assets/images/pic/bg-order.png" alt="" class="img-responsive " style="max-width:400px;">
                    <span class="txt-subbody" style="color:#5D6A68;">您还没有相关的订单</span>
                    <div class="txt-subbody py-2">
                        <span class="">没有找到订单？试试</span>
                        <span class="text-danger  px-1" @click="onChangeTab('全部',0)">查看全部</span>
                        <span>或</span>
                        <span class="text-danger px-1" @click="$router.push({path:'/login'})">切换帐号</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery';
    import {ElLoading,ElMessage} from 'element-plus';
    import {getOrderList} from 'network/order';
    import GoodsItem from 'components/content/goods/GoodsItem';

    import {reactive, ref, toRefs, onMounted} from 'vue';
    import {useRouter, useRoute} from "vue-router";


    export default {
        name: "Order",
        components: {
            GoodsItem,
        },
        setup() {
            /*List 组件通过 loading 和 finished 两个变量控制加载状态，
            当组件滚动到底部时，会触发 load 事件并将 loading 设置成 true。
            此时可以发起异步操作并更新数据，数据更新完毕后，将 loading
            设置成 false 即可。若数据已全部加载完毕，则直接将 finished 设
            置成 true 即可。*/
            const router = useRouter();
            const route = useRoute();
            let tabs = ref(null);
            const state = reactive({

                 loading: false,
                 finished: false,
                 refreshing: false,

                list: [],
                allList: [],            // 所有商品列表
                page: 1,                // 页面信息
                totalPage: 0,           // 所有页面
                status: 0,              // 订单状态
            });
            onMounted(() => {
                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '.content'});

                if(route.query.status !== undefined && route.query.status >= 0 && route.query.status <= 5) {
                    state.status = parseInt(route.query.status);     //  防止出现非整数
                }else{
                    state.status = 0;
                }

                $('._tab-order li:eq(' + state.status + ')').addClass('active').siblings('li').removeClass('active');


                onRefresh();
                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            });

            const loadData = () => {
                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '.content'});
                getOrderList({page: state.page, status: state.status, include: 'orderDetails.goods'}).then(res => {
                    state.list = state.list.concat(res.data);
                    // state.loading = false;

                    state.totalPage = res.meta.pagination.total_pages;
                    if (state.page >= state.totalPage) {
                        state.finished = true;         // 加载数据完毕就结束下拉加载
                    }
                    if (res.data.length !== 0) {
                        res.data.forEach(item => {
                            if (item.orderDetails.data && item.orderDetails.data.length > 0) {
                                state.allList.push(...item.orderDetails.data)
                            }
                        })
                    }
                    console.log("order-data",res);
                }).catch(err=>{
                    console.log("order-err",err);
                    ElMessage.error('请求数据失败！');
                });
                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            };
            const onLoad = () => {
                if (!state.refreshing && state.page < state.totalPage) {
                    state.page = state.page + 1;
                }

                if (state.refreshing) {
                    state.list = [];
                    state.allList = [];
                    state.refreshing = false;
                }
                loadData();

            };
            const onRefresh = () => {
                //是否处于加载状态，加载过程中不触发Load事件
                state.refreshing = true;
                //清空列表数据
                state.finished = false;
                //重新加载数据
                // 将loading设置为true，表示处于加载状态
                state.loading = true;
                state.page = 1;
                onLoad();
            };

            // 切换标签
            const onChangeTab = (name, index) => {
                $('._tab-order li:eq(' + index + ')').addClass('active').siblings('li').removeClass('active');
                state.status = index;
                onRefresh();
            };


            // 跳转订单详情页
            const goTo = (id) => {
                router.push({path: '/orderdetail', query: {id}});
            };


            return {...toRefs(state), loadData, onRefresh, tabs, onChangeTab, goTo};
        }
    }
</script>
<style scoped lang="scss">
    ._shopOrder {
        min-height: 600px;

        #_Order-box {
            max-width: 1200px;
            min-height:580px;
        }

        ._tab-order li {
            width: 20%;
        }

        ._tab-order li span {
            border-bottom: 3px solid white;
        }

        ._tab-order li.active span {
            border-bottom: 3px solid red;
        }

        .content {
            position: relative;
            width:100%;
            min-height:400px;
            /*left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            margin:0 auto;*/
            ._shopItemBox{
                border:2px solid #BAF3EA;
                border-radius:10px;
            }
        }
    }

</style>